/* manuale-regular - latin */
@font-face {
  font-family: 'Manuale';
  font-style: normal;
  font-weight: 400;
  src: url('manuale/manuale-v23-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('manuale/manuale-v23-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('manuale/manuale-v23-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('manuale/manuale-v23-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('manuale/manuale-v23-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('manuale/manuale-v23-latin-regular.svg#Manuale') format('svg'); /* Legacy iOS */
}
